import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-input-phone.scss';
import '@servicenow/now-dropdown';
import '@servicenow/now-button';
import focusBehavior from '@servicenow/behavior-focus';
import './now-input.js';
import {t} from 'sn-translate';
import {makeRedispatchEffect} from '@servicenow/library-enhanced-utils';
import {messagesSchema} from './schemas/messages-schema';

const handleCallButtonClick = (disabled, dispatch) => {
	dispatch(() => {
		return {
			type: 'NOW_INPUT_PHONE#BUTTON_CLICKED',
			payload: {},
			shouldDispatch: !disabled
		};
	});
};

const getNumberValue = (readonly, selectedCountryCode, number) => {
	if (readonly && selectedCountryCode) {
		return `+${selectedCountryCode} ${number}`;
	}

	return number;
};

const renderCountryCodeDropdown = (
	readonly,
	disabled,
	countryCodes,
	selectedCountryCode
) => {
	if (readonly) return;

	if (Array.isArray(countryCodes) && countryCodes.length) {
		return (
			<now-dropdown-custom-target
				className="now-m-inline-start--xs"
				slot="start"
				items={countryCodes.map((item) => {
					return {id: item.code, label: item.name};
				})}
				selected-items={[selectedCountryCode]}
				disable-listeners={disabled}
				bare
				enable-search>
				<now-button-bare
					slot="trigger"
					variant="primary"
					icon-end="caret-down-fill"
					label={`+${selectedCountryCode}`}
					disabled={disabled}
					config-aria={{
						'aria-label': t('Country code: +{0}', selectedCountryCode),
						'aria-haspopup': 'listbox',
						'aria-expanded': 'false'
					}}
					high-contrast
				/>
			</now-dropdown-custom-target>
		);
	}
};

const view = (state, {dispatch}) => {
	const {
		autofocus,
		countryCodes,
		disabled,
		helperContent,
		hideRequiredIndicator,
		invalid,
		label,
		manageInvalid,
		manageValue,
		max,
		maxlength,
		messages,
		min,
		minlength,
		name,
		number,
		optional,
		pattern,
		placeholder,
		readonly,
		required,
		selectedCountryCode = countryCodes &&
			countryCodes.length &&
			countryCodes[0].code
	} = state.properties;
	return (
		<now-input
			className="now-input"
			autofocus={autofocus}
			disabled={disabled}
			helper-content={helperContent}
			hide-required-indicator={hideRequiredIndicator}
			invalid={invalid}
			label={label}
			max={max}
			maxlength={maxlength}
			messages={messages}
			min={min}
			minlength={minlength}
			name={name}
			optional={optional}
			pattern={pattern}
			placeholder={placeholder}
			readonly={readonly}
			required={required}
			type="text"
			value={getNumberValue(readonly, selectedCountryCode, number)}
			manage-invalid={manageInvalid}
			manage-value={manageValue}>
			{renderCountryCodeDropdown(
				readonly,
				disabled,
				countryCodes,
				selectedCountryCode
			)}
			<now-button-iconic
				className="now-button-iconic"
				slot="end"
				size="sm"
				icon="phone-outline"
				variant="tertiary"
				bare
				disabled={disabled}
				on-click={handleCallButtonClick(disabled, dispatch)}
				tooltip-content={t('Call')}
				config-aria={{
					'aria-label': t('Call')
				}}
			/>
		</now-input>
	);
};

/**
 * Phone inputs provide a text box for a phone number,
 * including country and other identifying codes.
 *
 * ```jsx
 * <now-input-phone />
 * ```
 *
 * @seismicElement now-input-phone
 * @summary Form element for input of telephone numbers with country code and CTI.
 * @uib.label Input phone
 * @uib.icon input-phone
 * @uib.description Phone inputs provide a text box for a phone number, including country and other identifying codes
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, placeholder, countryCodes, selectedCountryCode, number, name, value, readonly, required, hideRequiredIndicator, optional, minlength, maxlength, pattern, autofocus, disabled, invalid, helperContent, messages
 * @uib.actions NOW_INPUT_PHONE#INVALID_SET, NOW_INPUT_PHONE#VALUE_SET, NOW_INPUT_PHONE#INPUT, NOW_INPUT_PHONE#BUTTON_CLICKED, NOW_INPUT_PHONE#COUNTRY_CODE_SET
 */
createEnhancedElement('now-input-phone', {
	properties: {
		/**
		 * When true, the focus is automatically placed inside the input
		 * on page load.
		 * @type {boolean}
		 * @uib.label Auto-focus
		 * @uib.description This property allows you to indicate that focus is automatically placed inside the input on page load.
		 */
		autofocus: {schema: {type: 'boolean'}},
		/**
		 * Array of country codes to display in the dropdown inside of the field.
		 * @type {Array.<{code: string, name: string}>}
		 * @uib.description Array of country codes to display in the dropdown inside of the field.
		 */
		countryCodes: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						code: {type: 'string'},
						name: {type: 'string', translatable: true}
					}
				}
			}
		},
		/**
		 * A `code` value matching an object from the `countryCodes` array that
		 * is selected in the country code dropdown menu.
		 * @type {string}
		 * @uib.description A `code` value matching an object from the `countryCodes` array that is selected in the country code dropdown menu
		 */
		selectedCountryCode: {schema: {type: 'string'}},
		/**
		 * When true, the user cannot interact with the field
		 * or change its value and the field appears greyed out.
		 * @type {boolean}
		 * @uib.description By enabling this property, the user cannot interact with the field and cannot change its value. The field will appear grayed out when disabled.
		 */
		disabled: {schema: {type: 'boolean'}},
		/**
		 * If set, an iconic button with a popover appears next to the field
		 * label to display additional hint text to the user, such as
		 * phone number requirements or other instructions for completing the field.
		 * @type {(string|JSX)}
		 * @uib.label Helper text
		 * @uib.fieldType string
		 * @uib.description If set, an iconic button with popover will appear next to the field label for displaying additional hint text to the user, such as password requirements or other instructions for completing the field. Use plain text to allow the text to be translatable. If you do not want to add helper content, leave this property empty.
		 * @uib.translatable true
		 */
		helperContent: {},
		/**
		 * When true, the required indicator is visually hidden. Use this for
		 * forms where most fields are required, along with the `optional` flag
		 * for any fields that are not required.
		 * @type {boolean}
		 * @uib.label Hide Required Indicator
		 * @uib.description When true, the required indicator is visually hidden. Use this for forms where most fields are required, along with the `optional` flag for any fields that are not required.
		 */
		hideRequiredIndicator: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, indicates that the field value is invalid,
		 * which is visually reflected in the field display.
		 * @type {boolean}
		 * @uib.description By enabling this property, the field will appear as visually invalid, indicating incorrect input.
		 */
		invalid: {manageable: true, schema: {type: 'boolean'}},
		/**
		 * User-visible label for the field.
		 * @type {string}
		 * @uib.description The label property allows you to tell users the value that should be entered in the input field.
		 * @uib.defaultValue Label
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Maximum value that the user can enter in the field.
		 * @type {number}
		 */
		max: {schema: {type: 'number'}},
		/**
		 * Maximum number of characters that the user can enter in the field.
		 * @type {number}
		 * @uib.label Max. characters
		 * @uib.fieldType number
		 * @uib.description The maxlength property allows you to set a maximum number of characters for the input field.
		 */
		maxlength: {schema: {type: 'number'}},
		/**
		 * Array of inline alert messages that are displayed below the field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: (string|JSX), icon: string}>}
		 * @uib.label Alert messages
		 * @uib.description Array of inline alert messages that are displayed below the field.
		 */
		messages: {
			default: [],
			schema: messagesSchema
		},
		/**
		 * Minimum value that the user can enter in the field.
		 * @type {number}
		 */
		min: {schema: {type: 'number'}},
		/**
		 * Minimum number of characters that the user can enter in the field.
		 * @type {number}
		 * @uib.label Min. characters
		 * @uib.fieldType number
		 * @uib.description This property allows you to set a minimum number of characters for the input field.
		 */
		minlength: {schema: {type: 'number'}},
		/**
		 * Name of the field control. Dispatched along with the field value
		 * on user interaction.
		 * @type {string}
		 * @uib.label Identifier
		 * @uib.description The name property allows you to set a reference name for the input field.
		 */
		name: {schema: {type: 'string'}},
		/**
		 * Portion of the phone number (excluding the country code) present
		 * in the main input area of the phone input field.
		 * @type {string}
		 * @uib.description Portion of the phone number (excluding the country code) present in the main input area of the phone input field.
		 */
		number: {schema: {type: 'string'}},
		/**
		 * When true, the field is optional and does not need to be completed
		 * before submitting the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user does not have to enter a value in the input field before proceeding.
		 */
		optional: {schema: {type: 'boolean'}},
		/**
		 * A regular expression against which the user input is validated.
		 * @type {string}
		 * @uib.label Validation pattern
		 * @uib.description The pattern property allows you to provide a regular expression for which user input is validated.
		 * @uib.fieldType string
		 * @uib.typeMetadata.emptyStateBehavior NULL
		 */
		pattern: {
			default: '[0-9]{3}-[0-9]{3}-[0-9]{4}',
			schema: {type: ['string', 'null']}
		},
		/**
		 * Hint text displayed in the input when the user has not yet entered a
		 * value.
		 * @type {string}
		 * @uib.description The placeholder property allows you to set a placeholder text for the input field. This text should hint to the user what input is expected.
		 * @uib.translatable true
		 */
		placeholder: {schema: {type: 'string'}},
		/**
		 * When true, the user cannot modify the value of the field.
		 * @type {boolean}
		 * @uib.label Read-only
		 * @uib.description By enabling this property, the user cannot modify the value of the input field. The input field is not disabled in this setting.
		 */
		readonly: {schema: {type: 'boolean'}},
		/**
		 * When true, the user must enter a value in the
		 * input field before they can submit the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user must enter a value in the input field before proceeding.
		 */
		required: {schema: {type: 'boolean'}},
		/**
		 * The value of the overall phone input field, including both the
		 * country code and numeric string.
		 * @type {string}
		 * @uib.description The value property allows you to set a value for the input field. Unless the input field has pre-populated data, this property should be left empty.
		 * @uib.translatable true
		 */
		value: {manageable: true, schema: {type: 'string'}}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks the CTI button.
		 * @type {{}}
		 * @uib.description Dispatched when the user clicks the CTI (phone) button.
		 */
		'NOW_INPUT_PHONE#BUTTON_CLICKED': {
			schema: {
				type: 'object'
			}
		},
		/**
		 * Dispatched when the user selects a country code from the dropdown.
		 * Payload contains the `code` value from the corresponding
		 * configuration object in the `countryCodes` array.
		 * @type {{value: string}}
		 * @uib.description Dispatched when the user selects a country code from the dropdown
		 */
		'NOW_INPUT_PHONE#COUNTRY_CODE_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'string'}
				}
			}
		},
		/**
		 * Dispatched when the user moves focus away from the field.
		 * Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `value`: Boolean. Value of the `invalid` property, which is based on
		 *   the values of the `required` and `pattern` properties.
		 * - `fieldValue`: String. Value entered in the field.
		 *
		 * Set the `manage-invalid` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: boolean, fieldValue: string}}
		 * @uib.description Dispatched when the field is blurred and the input value is invalid
		 */
		'NOW_INPUT_PHONE#INVALID_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'boolean'},
					fieldValue: {type: 'string'}
				}
			}
		},
		/**
		 * Dispatched when the user selects a country code from the dropdown
		 * or updates the main phone number input value.
		 * Payload contains the concatenation of the country code and phone number.
		 * Set the `manage-value` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: string}}
		 * @uib.description Dispatched when the field is blurred and the input value is valid
		 */
		'NOW_INPUT_PHONE#VALUE_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'boolean'}
				}
			}
		},
		/**
		 * Dispatched when the user enters data in the field by typing or
		 * pasting text. The system does not validate the value or update
		 * the `value` property. Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `data`: String. Value entered in the field that caused the event to fire.
		 * - `fieldValue`: String. Current value of the field when the event fired.
		 *
		 * @type {{name: string, data: string, fieldValue: string}}
		 * @uib.description Dispatched when the user enters data in the input field by typing or pasting text
		 */
		'NOW_INPUT_PHONE#INPUT': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					data: {type: 'string'},
					fieldValue: {type: 'string'}
				}
			}
		}
	},
	actionHandlers: {
		'NOW_DROPDOWN_CUSTOM_TARGET#SELECTED_ITEMS_SET': ({
			action,
			updateProperties,
			dispatch
		}) => {
			updateProperties({selectedCountryCode: action.payload.value[0]});
			dispatch(({properties}) => {
				return {
					type: 'NOW_INPUT_PHONE#COUNTRY_CODE_SET',
					payload: {
						name: properties.name,
						value: action.payload.value[0]
					}
				};
			});
			dispatch(({properties}) => {
				const fullNumber = action.payload.value[0] + '-' + properties.number;
				return {
					type: 'NOW_INPUT_PHONE#VALUE_SET',
					payload: {
						name: properties.name,
						value: fullNumber
					}
				};
			});
		},
		'NOW_INPUT#INVALID_SET': makeRedispatchEffect(
			'NOW_INPUT_PHONE#INVALID_SET'
		),
		'NOW_INPUT#INPUT': makeRedispatchEffect('NOW_INPUT_PHONE#INPUT'),
		'NOW_INPUT#VALUE_SET': ({action, dispatch}) => {
			dispatch(({properties}) => {
				const fullNumber =
					properties.selectedCountryCode + '-' + action.payload.value;
				return {
					type: 'NOW_INPUT_PHONE#VALUE_SET',
					payload: {
						name: properties.name,
						value: fullNumber
					}
				};
			});
		}
	},
	behaviors: [{behavior: focusBehavior}],
	styles,
	view
});
