import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-input-password.scss';
import {
	getFormFieldMessageIds,
	renderFormFieldMessages,
	renderFormFieldLabel
} from './form-field';
import {messagesSchema} from './schemas/messages-schema';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import focusBehavior from '@servicenow/behavior-focus';
import {t} from 'sn-translate';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_DOM_READY} = actionTypes;

const runPasswordValidation = (e, requirements, updateState, dispatch) => {
	const fieldValue = e.target.value;
	const data = e.data;
	let validations = requirements.map((req) => {
		if (!fieldValue) {
			return req;
		}
		const valid = new RegExp(req.pattern).test(fieldValue);
		return {
			...req,
			valid: valid
		};
	});
	updateState({validations});
	dispatch(({properties: {name}}) => {
		return {
			type: 'NOW_INPUT_PASSWORD#INPUT',
			payload: {name, data, fieldValue}
		};
	});
};

const handleBlur = (e, validations, updateState, dispatch) => {
	const {name, value} = e.target;
	const {valid} = e.target.validity;
	updateState({hasFocus: false});
	const requirementsNotMet =
		Array.isArray(validations) &&
		validations.length > 0 &&
		validations.some((req) => req.valid === false);
	dispatch(() => {
		return {
			type: 'NOW_INPUT_PASSWORD#INVALID_SET',
			payload: {
				name: name,
				value: Boolean(!valid || requirementsNotMet),
				fieldValue: value
			}
		};
	});
	updateState(({properties}) => {
		const {invalid} = properties;
		return {operation: 'set', validations: [], shouldUpdate: !invalid};
	});
	dispatch(({properties}) => {
		const {disabled, readonly, invalid, value: previousValue} = properties;
		return {
			type: 'NOW_INPUT_PASSWORD#VALUE_SET',
			payload: {name, value},
			shouldDispatch:
				!disabled && !readonly && !invalid && value !== previousValue
		};
	});
};

const handleMaskButtonClick = (disabled, updateState) => {
	updateState(({state}) => {
		return {
			operation: 'set',
			path: 'masked',
			value: !state.masked,
			shouldUpdate: !disabled
		};
	});
};

const getMessagesAndValidations = (compact, messages, validations = []) => {
	if (compact) {
		const validationErrors = validations
			.filter((req) => req.valid === false)
			.map((req) => {
				return {
					status: 'critical',
					icon: 'exclamation-circle-outline',
					content: req.label
				};
			});
		return [...validationErrors, ...messages];
	}
	return messages;
};

const renderValidations = (compact, validations, componentId) => {
	if (compact) {
		return;
	}
	return validations.map((req, index) => {
		let validity;
		if (req.hasOwnProperty('valid')) {
			validity = req.valid ? 'valid' : 'invalid';
		}
		return (
			<span
				class={{
					'now-input-password-validation': true,
					['-' + validity]: validity
				}}>
				<now-icon
					id={`${componentId}-message-${index}`}
					className="now-m-inline-end--xs"
					icon={
						validity === 'valid'
							? 'check-circle-fill'
							: validity === 'invalid'
							? 'exclamation-circle-outline'
							: 'check-circle-outline'
					}
					aria-label={
						validity === 'valid'
							? t('Met requirement: {0}.', req.label)
							: t('Unmet requirement: {0}.', req.label)
					}
					role="img"
				/>
				<span aria-hidden="true">{req.label}</span>
			</span>
		);
	});
};

const renderMaskButton = (
	disableUnmask,
	masked,
	disabled,
	readonly,
	updateState
) => {
	if (!disableUnmask) {
		return (
			<div className="now-align">
				<now-button-iconic
					className="now-input-password-mask-button"
					slot="end"
					size="sm"
					icon={masked ? 'eye-outline' : 'eye-slash-outline'}
					variant="tertiary"
					bare
					disabled={disabled || readonly}
					on-click={() => handleMaskButtonClick(disabled, updateState)}
					tooltip-content={masked ? t('Unmask password') : t('Mask password')}
					config-aria={{
						'aria-label': masked ? t('Unmask password') : t('Mask password')
					}}
				/>
			</div>
		);
	}
};

const view = (state, {updateState, dispatch}) => {
	const {
		compact,
		disabled,
		disableUnmask,
		hideRequiredIndicator,
		invalid,
		label,
		messages,
		name,
		optional,
		placeholder,
		readonly,
		required,
		requirements,
		value
	} = state.properties;
	const {componentId, masked, validations, hasFocus, hasHover} = state;

	const messagesAndValidations = getMessagesAndValidations(
		compact,
		messages,
		validations
	);

	const allMessages = compact
		? messagesAndValidations
		: [...requirements, ...messagesAndValidations];

	const startIndex = compact ? 0 : requirements.length;

	const helperContent = compact ? (
		<ul style={{'list-style': 'none', margin: '0', padding: '1rem'}}>
			{requirements.map((req) => {
				return <li>&bull; {req.label}</li>;
			})}
		</ul>
	) : (
		undefined
	);

	return (
		<div className="now-input">
			{renderFormFieldLabel(
				componentId,
				invalid,
				required,
				optional,
				label,
				helperContent,
				hideRequiredIndicator
			)}
			<div
				class={{
					'now-input-field': true,
					'now-form-field': true,
					'is-invalid': invalid,
					'is-disabled': disabled,
					'is-readonly': readonly,
					'has-focus': hasFocus,
					'has-hover': hasHover
				}}>
				<input
					id={`form-field-${componentId}`}
					className="now-input-native"
					aria-describedby={getFormFieldMessageIds(componentId, allMessages)}
					aria-required={required ? 'true' : 'false'}
					aria-invalid={invalid ? 'true' : 'false'}
					disabled={disabled}
					name={name}
					placeholder={placeholder}
					readonly={readonly}
					required={required}
					type={masked ? 'password' : 'text'}
					value={value}
					on-mouseenter={() => updateState({hasHover: true})}
					on-mouseleave={() => updateState({hasHover: false})}
					on-input={(e) =>
						runPasswordValidation(e, requirements, updateState, dispatch)
					}
					on-focus={() => updateState({hasFocus: true})}
					on-blur={(e) => handleBlur(e, validations, updateState, dispatch)}
				/>
				{renderMaskButton(
					disableUnmask,
					masked,
					disabled,
					readonly,
					updateState
				)}
			</div>
			{renderValidations(compact, validations || requirements, componentId)}
			{renderFormFieldMessages(componentId, messagesAndValidations, startIndex)}
		</div>
	);
};

/**
 * The password input accepts a password and includes a label to help guide the user.
 *
 * ```jsx
 * <now-input-password label="Enter new password" />
 * ```
 *
 * @seismicElement now-input-password
 * @summary Form element for inputting passwords.
 * @uib.label Input password
 * @uib.icon input-password
 * @uib.description Form element for passwords
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, placeholder, name, value, readonly, required, hideRequiredIndicator, optional, disableUnmask, compact, requirements, autofocus, disabled, invalid, messages
 * @uib.actions NOW_INPUT_PASSWORD#INVALID_SET, NOW_INPUT_PASSWORD#VALUE_SET, NOW_INPUT_PASSWORD#INPUT
 */
createEnhancedElement('now-input-password', {
	properties: {
		/**
		 * When true, the focus is automatically placed inside the input
		 * on page load.
		 * @type {boolean}
		 * @uib.label Auto-focus
		 * @uib.description This property allows you to indicate that focus is automatically placed inside the input on page load.
		 */
		autofocus: {schema: {type: 'boolean'}},
		/**
		 * When true, password requirements display in the input field popover
		 * instead of below the input field. Validation errors for passwords
		 * that fail to meet requirements appear below the input field.
		 * @type {boolean}
		 * @uib.description When true, password requirements display in the input field popover instead of below the input field. Validation errors for passwords that fail to meet requirements appear below the input field
		 */
		compact: {schema: {type: 'boolean'}},
		/**
		 * When true, the user cannot interact with the field
		 * or change its value and the field appears greyed out.
		 * @type {boolean}
		 * @uib.description By enabling this property, the user cannot interact with the field and cannot change its value. The field will appear grayed out when disabled.
		 */
		disabled: {schema: {type: 'boolean'}},
		/**
		 * When true, disables the user's ability to view the password
		 * value in plain text using a `mask`/`unmask` button.
		 * @type {boolean}
		 * @uib.description disables the user's ability to view the password
		 * value in plain text using a `mask`/`unmask` button
		 */
		disableUnmask: {schema: {type: 'boolean'}},
		/**
		 * When true, the required indicator is visually hidden. Use this for
		 * forms where most fields are required, along with the `optional` flag
		 * for any fields that are not required.
		 * @type {boolean}
		 * @uib.label Hide Required Indicator
		 * @uib.description When true, the required indicator is visually hidden. Use this for forms where most fields are required, along with the `optional` flag for any fields that are not required.
		 */
		hideRequiredIndicator: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, indicates that the field value is invalid,
		 * which is visually reflected in the field display.
		 * @type {boolean}
		 * @uib.description By enabling this property, the field will appear as visually invalid, indicating incorrect input.
		 */
		invalid: {manageable: true, schema: {type: 'boolean'}},
		/**
		 * User-visible label for the field.
		 * @type {string}
		 * @uib.description The label property allows you to tell users the value that should be entered in the input field.
		 * @uib.defaultValue Label
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Array of inline alert messages that are displayed below the field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: (string|JSX), icon: string}>}
		 * @uib.label Alert messages
		 * @uib.description Array of inline alert messages that are displayed below the field.
		 */
		messages: {
			default: [],
			schema: messagesSchema
		},
		/**
		 * Name of the field control. Dispatched along with the field value
		 * on user interaction.
		 * @type {string}
		 * @uib.label Identifier
		 * @uib.description The name property allows you to set a reference name for the input field.
		 */
		name: {schema: {type: 'string'}},
		/**
		 * When true, the field is optional and does not need to be completed
		 * before submitting the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user does not have to enter a value in the input field before proceeding.
		 */
		optional: {schema: {type: 'boolean'}},
		/**
		 * Hint text displayed in the input when the user has not yet entered a
		 * value.
		 * @type {string}
		 * @uib.label Placeholder text
		 * @uib.description The placeholder property allows you to set a placeholder text for the input field. This text should hint to the user what input is expected.
		 * @uib.translatable true
		 */
		placeholder: {schema: {type: 'string'}},
		/**
		 * When true, the user cannot modify the value of the field.
		 * @type {boolean}
		 * @uib.label Read-only
		 * @uib.description By enabling this property, the user cannot modify the value of the input field. The input field is not disabled in this setting.
		 */
		readonly: {schema: {type: 'boolean'}},
		/**
		 * When true, the user must enter a value in the
		 * input field before they can submit the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user must enter a value in the input field before proceeding.
		 */
		required: {schema: {type: 'boolean'}},
		/**
		 * Array of requirements for the user-entered password. Each requirement
		 * must contain a label describing the requirement and the RegEx pattern
		 * against which the password must validate.
		 * @type {Array.<{label: string, pattern: RegExp}>}
		 * @uib.description Array of requirements for the user-entered password. Each requirement must contain a label describing the requirement and the RegEx pattern against which the password must validate
		 */
		requirements: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string', translatable: true},
						pattern: {type: 'string'}
					}
				}
			}
		},
		/**
		 * The value of the input field.
		 * @type {string}
		 * @uib.description The value property allows you to set a value for the input field. Unless the input field has pre-populated data, this property should be left empty.
		 * @uib.translatable true
		 */
		value: {manageable: true, schema: {type: 'string'}}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({host, properties}) => {
			if (properties.autofocus) {
				const input = host.shadowRoot.querySelector('input');
				if (input) {
					input.focus();
				}
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user moves focus away from the field.
		 * Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `value`: Boolean. Value of the `invalid` property, which is based on
		 *   the values of the `required` and `pattern` properties.
		 * - `fieldValue`: String. Value entered in the field.
		 *
		 * Set the `manage-invalid` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: boolean, fieldValue: string}}
		 * @uib.description Dispatched when the user moves focus away from the field and the value is invalid
		 */
		'NOW_INPUT_PASSWORD#INVALID_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'boolean'},
					fieldValue: {type: 'string'}
				}
			}
		},
		/**
		 * Dispatched when the user moves focus away from the field
		 * and the value is valid. Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `value`: String. Value entered in the field.
		 *
		 * Set the `manage-value` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: string}}
		 * @uib.description Dispatched when the user moves focus away from the field and the value is valid
		 */
		'NOW_INPUT_PASSWORD#VALUE_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'string'}
				}
			}
		},
		/**
		 * Dispatched when the user enters data in the field by typing or
		 * pasting text. The system does not validate the value or update
		 * the `value` property. Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `data`: String. Value entered in the field that caused the event to fire.
		 * - `fieldValue`: String. Current value of the field when the event fired.
		 * @type {{name: string, data: string, fieldValue: string}}
		 * @uib.description Dispatched when the user enters data in the field by typing or pasting text
		 */
		'NOW_INPUT_PASSWORD#INPUT': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					data: {type: 'string'},
					fieldValue: {type: 'string'}
				}
			}
		}
	},
	initialState: {
		masked: true
	},
	behaviors: [{behavior: focusBehavior}],
	styles,
	view
});
