import '@servicenow/now-icon';

export const getFormFieldMessageIds = (componentId, messages) => {
	if (Array.isArray(messages) && messages.length) {
		return messages
			.map((message, index) => {
				return `${componentId}-message-${index}`;
			})
			.join(' ');
	}
};

export const renderFormFieldMessages = (
	componentId,
	messages,
	startIndex = 0
) => {
	if (Array.isArray(messages) && messages.length) {
		return messages.map(({status = 'suggestion', icon, content}, index) => (
			<div
				id={`${componentId}-message-${index + startIndex}`}
				class={{
					'now-form-field-message': true,
					['-' + status]: status
				}}>
				{icon ? (
					<now-icon className="now-m-inline-end--xs" size="sm" icon={icon} />
				) : (
					undefined
				)}
				<p className="now-form-field-message-label">
					<span className="now-line-height-crop">{content}</span>
				</p>
			</div>
		));
	}
};
