import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-input.scss';
import {
	getFormFieldMessageIds,
	renderFormFieldMessages,
	renderFormFieldLabel
} from './form-field';
import {messagesSchema} from './schemas/messages-schema';
import focusBehavior from '@servicenow/behavior-focus';
import {isIPAddress} from './utils/ipaddress';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_DOM_READY, COMPONENT_PROPERTY_CHANGED} = actionTypes;

const handleInput = (e, dispatch) => {
	const fieldValue = e.target.value;
	const data = e.data;
	dispatch(({properties: {name}}) => {
		return {type: 'NOW_INPUT#INPUT', payload: {name, data, fieldValue}};
	});
};

/**
 * Strips leading zeros from an IP. If this changes the value then update
 * the "value" property and ensure the native input reflect the correct
 * value
 */
const stripIPZeros = (value, updateProperties, nativeInput) => {
	const newVal = value.replace(/\b0+\B/g, '');
	if (newVal !== value) {
		updateProperties(() => {
			return {
				path: 'value',
				value: newVal,
				shouldUpdate: true
			};
		});
		// need to explicitly force the native input value as the
		// property update might be skipped if the value is "the same", e.g
		// "192.168.0.1" someone types one more 0 => "192.168.00.1" => we
		// strip that 0 and the property update won't trigger a re-render
		nativeInput.value = newVal;
	}
	return newVal;
};

const handleBlur = (
	e,
	updateState,
	dispatch,
	type,
	updateProperties,
	properties
) => {
	let valid,
		{value} = e.target,
		forceValueChange = false;
	const {name} = e.target;

	if (type === 'ip' && value) {
		const oldVal = value;
		value = stripIPZeros(value, updateProperties, e.target);
		forceValueChange = value !== oldVal && value !== properties.value;
		valid = e.target.validity.valid && isIPAddress(value);
	} else {
		valid = e.target.validity.valid;
	}
	updateState({hasFocus: false});
	dispatch(() => {
		return {
			type: 'NOW_INPUT#INVALID_SET',
			payload: {name, value: !valid, fieldValue: value, type}
		};
	});
	dispatch(({properties}) => {
		const {disabled, readonly, invalid, value: previousValue} = properties;
		return {
			type: 'NOW_INPUT#VALUE_SET',
			payload: {name, value},
			shouldDispatch:
				!disabled &&
				!readonly &&
				!invalid &&
				(value !== previousValue || forceValueChange)
		};
	});
};

const view = (state, {dispatch, updateState, updateProperties}) => {
	const {componentId, hasFocus, hasHover} = state;
	const {
		disabled,
		helperContent,
		hideRequiredIndicator,
		invalid,
		label,
		max,
		maxlength,
		messages,
		min,
		minlength,
		multiple,
		name = `input-${componentId}`,
		optional,
		pattern,
		placeholder,
		readonly,
		required,
		step,
		type,
		value
	} = state.properties;

	return (
		<div className="now-input">
			{renderFormFieldLabel(
				componentId,
				invalid,
				required,
				optional,
				label,
				helperContent,
				hideRequiredIndicator
			)}
			<div
				class={{
					'now-input-field': true,
					'now-form-field': true,
					'is-invalid': invalid,
					'is-disabled': disabled,
					'is-readonly': readonly,
					'has-focus': hasFocus,
					'has-hover': hasHover
				}}>
				<div className="now-align">
					<slot name="start" />
				</div>
				<input
					id={`form-field-${componentId}`}
					className="now-input-native"
					aria-describedby={getFormFieldMessageIds(componentId, messages)}
					aria-required={required ? 'true' : 'false'}
					aria-invalid={invalid ? 'true' : 'false'}
					disabled={disabled}
					max={max}
					maxlength={maxlength}
					min={min}
					minlength={minlength}
					multiple={multiple}
					name={name}
					pattern={pattern}
					placeholder={placeholder}
					readonly={readonly}
					required={required}
					step={step}
					type={type}
					value={!value && readonly ? 'â€”' : value}
					on-mouseenter={() => updateState({hasHover: true})}
					on-mouseleave={() => updateState({hasHover: false})}
					on-focus={
						disabled || readonly
							? undefined
							: () => updateState({hasFocus: true})
					}
					on-blur={(e) =>
						handleBlur(
							e,
							updateState,
							dispatch,
							type,
							updateProperties,
							state.properties
						)
					}
					on-input={(e) => handleInput(e, dispatch)}
				/>
				<div className="now-align">
					<slot name="end" />
				</div>
			</div>
			{renderFormFieldMessages(componentId, messages)}
		</div>
	);
};

/**
 * Accepts user input in the form of text, values, or symbols.
 *
 * ```jsx
 * <now-input label="First Name" placeholder="Susan" />
 *
 * <now-input label="Email" placeholder="susan@email.net">
 *   <now-button-iconic slot="start" icon="envelope-outline" variant="tertiary" bare />
 * </now-input>
 * ```
 *
 * @seismicElement now-input
 * @summary Accepts user input in the form of text, values, or symbols.
 * @uib.label Input
 * @uib.icon input
 * @uib.description Accepts user input in the form of text, values, or symbols.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties type, label, placeholder, name, value, readonly, required, hideRequiredIndicator, optional, min, max, minlength, maxlength, multiple, pattern, autofocus, disabled, invalid, helperContent, messages
 * @uib.actions NOW_INPUT#INVALID_SET, NOW_INPUT#VALUE_SET, NOW_INPUT#INPUT
 */
createEnhancedElement('now-input', {
	properties: {
		/**
		 * When true, the focus is automatically placed inside the input
		 * on page load.
		 * @type {boolean}
		 * @uib.label Auto-focus
		 * @uib.description This property allows you to indicate that focus is automatically placed inside the input on page load.
		 */
		autofocus: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, the user cannot interact with the field
		 * or change its value and the field appears greyed out.
		 * @type {boolean}
		 * @uib.description By enabling this property, the user cannot interact with the field and cannot change its value. The field will appear grayed out when disabled.
		 */
		disabled: {default: false, schema: {type: 'boolean'}},
		/**
		 * If set, an iconic button with a popover appears next to the field
		 * label to display additional hint text to the user, such as
		 * password requirements or other instructions for completing the field.
		 * @type {(string|JSX)}
		 * @uib.label Helper Content
		 * @uib.fieldType string
		 * @uib.description If set, an iconic button with popover will appear next to the field label for displaying additional hint text to the user, such as password requirements or other instructions for completing the field. Use plain text to allow the text to be translatable. If you do not want to add helper content, leave this property empty.
		 * @uib.translatable true
		 */
		helperContent: {},
		/**
		 * When true, the required indicator is visually hidden. Use this for
		 * forms where most fields are required, along with the `optional` flag
		 * for any fields that are not required.
		 * @type {boolean}
		 * @uib.label Hide Required Indicator
		 * @uib.description When true, the required indicator is visually hidden. Use this for forms where most fields are required, along with the `optional` flag for any fields that are not required.
		 */
		hideRequiredIndicator: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, indicates that the field value is invalid,
		 * which is visually reflected in the field display.
		 * @type {boolean}
		 * @uib.description By enabling this property, the field will appear as visually invalid, indicating incorrect input.
		 */
		invalid: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * User-visible label for the field.
		 * @type {string}
		 * @uib.description The label property allows you to tell users the value that should be entered in the input field.
		 * @uib.defaultValue Label
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Maximum value that the user can enter in the field.
		 * @type {number}
		 * @uib.label Max. value
		 * @uib.fieldType number
		 * @uib.description The max property allows you to set a maximum number value for the number input field.
		 */
		max: {schema: {type: ['number', 'null']}},
		/**
		 * Maximum number of characters that the user can enter in the field.
		 * @type {number}
		 * @uib.label Max. characters
		 * @uib.fieldType number
		 * @uib.description The maxlength property allows you to set a maximum number of characters for the input field.
		 */
		maxlength: {schema: {type: ['number', 'null']}},
		/**
		 * Array of inline alert messages that display below the field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: (string|JSX), icon: string}>}
		 * @uib.label Alert messages
		 * @uib.description Array of inline alert messages that are displayed below the field.
		 */
		messages: {
			default: [],
			schema: messagesSchema
		},
		/**
		 * Minimum value that the user can enter in the field.
		 * @type {number}
		 * @uib.label Min. value
		 * @uib.fieldType number
		 * @uib.description The minimum property allows you to set a minimum number value for the number input field.
		 */
		min: {schema: {type: ['number', 'null']}},
		/**
		 * Minimum number of characters that the user can enter in the field.
		 * @type {number}
		 * @uib.label Min. characters
		 * @uib.fieldType number
		 * @uib.description This property allows you to set a minimum number of characters for the input field.
		 */
		minlength: {schema: {type: ['number', 'null']}},
		/**
		 * When true, the user can enter multiple comma-separated email
		 * addresses in inputs of `type` "email".
		 * @type {boolean}
		 * @uib.label Multiple addresses allowed
		 * @uib.description By enabling this property, the user can enter multiple comma-separated email addresses.
		 */
		multiple: {schema: {type: 'boolean'}},
		/**
		 * Name of the field control. Dispatched along with the field value
		 * when the user interacts with the field.
		 * @type {string}
		 * @uib.label Identifier
		 * @uib.description The name property allows you to set a reference name for the input field.
		 */
		name: {schema: {type: 'string'}},
		/**
		 * When true, the field is optional and does not need to be completed
		 * before submitting the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user does not have to enter a value in the input field before proceeding.
		 */
		optional: {default: false, schema: {type: 'boolean'}},
		/**
		 * A regular expression against which user input is validated.
		 * The applicable regex for each IP address format overrides this property
		 * if the `type` is set to "ipv4" or "ipv6".
		 * @type {string}
		 * @uib.label Validation pattern
		 * @uib.description The pattern property allows you to provide a regular expression for which user input is validated.
		 * @uib.fieldType string
		 * @uib.typeMetadata.emptyStateBehavior NULL
		 */
		pattern: {schema: {type: ['string', 'null']}},
		/**
		 * Hint text displayed in the input when the user has not yet entered a
		 * value.
		 * @type {string}
		 * @uib.description The placeholder property allows you to set a placeholder text for the input field. This text should hint to the user what input is expected.
		 * @uib.translatable true
		 */
		placeholder: {schema: {type: 'string'}},
		/**
		 * When true, the user cannot modify the value of the field.
		 * @type {boolean}
		 * @uib.label Read Only
		 * @uib.description By enabling this property, the user cannot modify the value of the input field. The input field is not disabled in this setting.
		 */
		readonly: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, the user must enter a value in the
		 * input field before they can submit the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user must enter a value in the input field before proceeding.
		 */
		required: {default: false, schema: {type: 'boolean'}},
		/**
		 * For inputs of `type` "number", defines the valid input values.
		 * @type {(number|'any')}
		 * @uib.description For inputs of type "number" defined the granularity that a numeric value must adhere to in order to be a valid value. A number or "any"
		 * @uib.fieldType
		 */
		step: {
			default: 'any',
			schema: {oneOf: [{type: 'number'}, {const: 'any'}]}
		},
		/**
		 * The type of control to render, based on the type of expected value.
		 * @type {('email'|'ip'|'number'|'text')}
		 * @uib.description The type property allows you to adjust the expected input type.
		 */
		type: {
			default: 'text',
			schema: {
				type: 'string',
				enum: ['email', 'ip', 'number', 'text']
			}
		},
		/**
		 * The value of the input field.
		 * @type {string}
		 * @uib.description The value property allows you to set a value for the input field. Unless the input field has pre-populated data, this property should be left empty.
		 * @uib.translatable true
		 */
		value: {manageable: true, schema: {type: 'string'}}
	},
	dispatches: {
		/**
		 * Dispatched when the user moves focus away from the field.
		 * Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `value`: Boolean. Value of the `invalid` property, which is based on
		 *   the values of the `required` and `pattern` properties.
		 * - `fieldValue`: String. Value entered in the field.
		 * - `type`: String. Value of the `type` property.
		 *
		 * Set the `manage-invalid` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: boolean, fieldValue: string, type: string}}
		 * @uib.description Dispatched when the field is blurred
		 */
		'NOW_INPUT#INVALID_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'boolean'},
					fieldValue: {type: 'string'},
					type: {type: 'string'}
				}
			}
		},
		/**
		 * Dispatched when the user moves focus away from the field
		 * and the value is valid. Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `value`: String. Value entered in the field.
		 *
		 * Set the `manage-value` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: string}}
		 * @uib.description Dispatched when the field is blurred and the input value is valid
		 */
		'NOW_INPUT#VALUE_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'boolean'}
				}
			}
		},
		/**
		 * Dispatched when the user enters data in the field by typing or
		 * pasting text. The system does not validate the value or update
		 * the `value` property. Payload contains these properties:
		 * - `name`: String. Value of the `name` property.
		 * - `data`: String. Value entered in the field that caused the event to fire.
		 * - `fieldValue`: String. Current value of the field when the event fired.
		 * @type {{name: string, data: string, fieldValue: string}}
		 * @uib.description Dispatched when the user enters data in the input field by typing or pasting text
		 */
		'NOW_INPUT#INPUT': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					data: {type: 'string'},
					fieldValue: {type: 'string'}
				}
			}
		}
	},
	slots: {
		/**
		 * Slot for field decorators. Located at the end
		 * of the input field.
		 */
		end: {},
		/**
		 * Slot for field decorators. Located at the beginning
		 * of the input field.
		 */
		start: {}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({host, properties, updateProperties}) => {
			let input;
			if (properties.autofocus) {
				input = host.shadowRoot.querySelector('input');
				if (input) {
					input.focus();
				}
			}

			const {value, type} = properties;
			if (value && type === 'ip') {
				input = input ? input : host.shadowRoot.querySelector('input');
				stripIPZeros(value, updateProperties, input);
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			host,
			action,
			properties,
			dispatch,
			updateProperties
		}) => {
			const {name} = action.payload;
			if (name === 'value' || name === 'pattern' || name === 'type') {
				let valid;
				const nativeInput = host.shadowRoot.querySelector('input');
				const {name, type} = properties;
				let {value} = properties;

				if (type === 'ip' && value) {
					value = stripIPZeros(value, updateProperties, nativeInput);
					valid = nativeInput.validity.valid && isIPAddress(value);
				} else {
					valid = nativeInput.validity.valid;
				}
				dispatch(() => {
					return {
						type: 'NOW_INPUT#INVALID_SET',
						payload: {name, value: !valid, fieldValue: value, type}
					};
				});
			}
		}
	},
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	styles,
	view
});
