import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-popover';
import {t} from 'sn-translate';
import {createCustomElement} from '@servicenow/ui-core';

createCustomElement('now-form-popover-content', {
	view: (state) => (
		<div style={{padding: '0.25rem'}}>{state.properties.content}</div>
	),
	properties: {
		content: {}
	}
});

const renderFormFieldRequiredOrOptional = (
	required,
	optional,
	hideRequiredIndicator
) => {
	if (required && !hideRequiredIndicator) {
		return (
			<now-icon
				className="now-m-inline-start--xs"
				icon="asterisk-fill"
				size="sm"
			/>
		);
	} else if (optional) {
		return (
			<span className="now-m-inline-start--xs now-line-height-crop">
				({t('Optional')})
			</span>
		);
	}
};

const renderFormFieldHelper = (helperContent, invalid) => {
	if (helperContent) {
		return (
			<now-popover className="now-form-field-popover" interaction-type="none">
				<now-button-iconic
					slot="trigger"
					config-aria={{
						button: {'aria-label': t('More information')}
					}}
					class={{'now-form-field-button': true, 'is-invalid': invalid}}
					icon="circle-info-outline"
					size="sm"
					variant="tertiary"
					bare
					hide-padding
				/>
				<now-form-popover-content
					slot="content"
					content={helperContent}
					role="status"
				/>
			</now-popover>
		);
	}
};

export const renderFormFieldLabel = (
	componentId,
	invalid,
	required,
	optional,
	label,
	helperContent,
	hideRequiredIndicator
) => {
	return (
		<div className="now-align -block-center now-m-block-end--xxs">
			<label
				class={{
					'now-form-field-label': true,
					'is-invalid': invalid
				}}
				htmlFor={`form-field-${componentId}`}
				id={`form-field-${componentId}-label`}>
				<span className="now-line-height-crop">
					<span title={label} className="will-truncate">
						{label}
					</span>
				</span>
				{renderFormFieldRequiredOrOptional(
					required,
					optional,
					hideRequiredIndicator
				)}
			</label>
			{renderFormFieldHelper(helperContent, invalid)}
		</div>
	);
};
